<!--  -->
<template>
<div class='searchResult'>
    <div class="top">
   <div class="searchbox">
    <van-search
     v-model="keyword"
     right-icon="" 
     left-icon=""
     shape="round"
     show-action
     placeholder="请输入搜索关键词"
     
     @cancel="onCancel"
      />
   </div>
   <div class="dropdownMenu">
       <van-dropdown-menu>
   <van-dropdown-item v-model="value1" :options="option1" />
   <van-dropdown-item v-model="value2" :options="option2" />
   <van-dropdown-item v-model="value3" :options="option3" />
   </van-dropdown-menu>
   </div>
   </div>
   <div class="resultList">
       <div class="book" v-for="(book,index) in resultBook" :key="index">
            <div class="img" @click.stop="getBookDetail(book.bookId,book.title)">
               <img :src="book.cover" alt="">
            </div>
          <div class="book-content">
              <div>
                 <div class="title">
                     {{book.title}}
                 </div>
                 <div class="author">
                     {{book.author}} / {{book.category}}
                 </div>
                 </div>
                 <div class="summary">
                     {{book.summary}}
                 </div>
             </div>
         </div>
   </div>
</div>
</template>

<script>
import { Toast } from 'vant';

export default {
props:{},
data() {
//这里存放数据
return {
    keyword:'',
    resultBook:[],
     value1: 0,
      value2: 5,
      value3: 'a',
      option1: [
        { text: '综合排序', value: 0 },
        { text: '销量最高', value: 1 },
        { text: '人气最旺', value: 2 },
        { text: '最新上架', value: 3 },
        { text: '最近更新', value: 4 },
      ],
      option2: [
        { text: '全部频道', value: 5 },
        { text: '男生', value: 6 },
        { text: '女生', value: 7 },
        { text: '出版', value: 8 },
      ],
      option3: [
        { text: '筛选', value: 'a' },
      ],
};
},
//监听属性 类似于data概念
computed: {},
//监控data中的数据变化
watch: {},
//方法集合
methods: {
   onCancel() {
      Toast('取消');
      this.$router.push({name:'BookCity'});
    },
   async getResultData(){
     await this.axios.get(`/search/book.json?keyword=${this.keyword}`).then(re=>{
       this.resultBook=re.books;
       console.log('this.resultBook',this.resultBook);
     })
   },
    getBookDetail(id, title) {
      this.$router.push({ name: "BookDetails", params: { id, title } });
    },
},
//生命周期 - 创建完成（可以访问当前this实例）
created() {
  this.keyword=this.$route.params.word;
  this.getResultData();
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
components: {},
}
</script>
<style lang='scss' scoped>
  .back {
      padding: 2vh;
      width: 4vw;
      height: 4vh;
    }
   .top{
       position: fixed;
       top: 0;
       left: 0;
       right: 0;
       bottom: 0;
       z-index: 99;
   }
.resultList {
    height: 26vh;
    margin-top: 16vh;
    //   display: flex;
    
  .book {
      display: flex;
      padding: 10px;
    .img {
        height: 3.5rem;
        // width: 2.5rem;
        flex: 1;
      img {
       height: 100%;
       width: 100%;
       display: block;
      }
    }

    .book-content {
        // width:7.5rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 0.5rem;
        flex: 3;
      .title {
       font-size: 16px;
       font-weight: bold;
      }

      .author {
        font-size: 12px;
        padding-top: 5px;
      }

      .summary {
          overflow : hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2; /* 限制在一个块元素显示的文本的行数 */
      -webkit-box-orient: vertical; /* 垂直排列 */
      word-break: break-all;  /* 内容自动换行 */
      }
    }
  }
}
</style>